body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

  overflow: hidden;
}

.container {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;

    height: 99vh;
}
.answer-container {
    display: flex;
    justify-content: center;
    font-size: 2em;
    transition: 0.5s;
}

.answer-container > * {
    padding: 0.5em;
    border-radius: 10px;
    margin: 0.1em;
    font-weight: bold;
}

.answer-button {
    color: white;
    background-color: #ff9900;
    border: solid #ffad3c;
}

.previous-container {
    display: flex;
    justify-content: center;
    color: gray;
}

.previous-display {
    font-weight: bold;
}

.correct {
    color: green;
}

.incorrect {
    color: red;
}

.hud-container {
    display: flex;
    justify-content: space-around;
}
.equation-container {
    display: flex;
    justify-content: center;
    font-size: 2.3vw;

    height: 40%;
}
